.message {
  background-color: rgb(75, 219, 255);
  text-align: center;
  color: black;
  border: 2px solid black;
  width: 50vw;
  min-height: 50vh;
  z-index: 1;
  padding-bottom: 5vmin;
}

.message input {
  max-width: 30vw;
  font-size: 0.8em;
  text-align: center;
  border: 1px solid black;
}
.close {
  font-size: 0.8em;
  border: 1px solid black;
  box-shadow: -2px 2px 2px rgb(36, 36, 36);
}
.message button {
  font-size: 0.8em;
  border: 1px solid black;
}

.message button:hover {
  opacity: 0.5;
}
